import React, { useState, useEffect } from 'react';
import { Card, CardBody, CardText } from 'reactstrap';
import IntlMessages from '../../helpers/IntlMessages';
import { useDispatch, useSelector } from 'react-redux';
import { InsertPendiente, UpdatePendiente } from '../../Dusk/DashboardDusk';
import { Colxx } from '../../components/common/CustomBootstrap';
import Form from 'reactstrap/lib/Form';
import FormGroup from 'reactstrap/lib/FormGroup';
import Label from 'reactstrap/lib/Label';
import Input from 'reactstrap/lib/Input';
import Button from 'reactstrap/lib/Button';
import SingleLightbox from '../../components/pages/SingleLightbox';

const UploadPhoto = () => {
  const disparador = useDispatch();
  const [lastChecked, setLastChecked] = useState(false);
  const [TituloNormal, setTituloNormal] = useState();
  const [ItemSelect, setItemSelect] = useState({ estado: '', id: '' });
  const usuario = useSelector((store) => store.Dash);
  const insertPendientre = async () => {
    disparador(InsertPendiente(usuario.user.user[0].id_user, TituloNormal));
    setTituloNormal('');
  };

  useEffect(() => {
    if (lastChecked === true) {
      setLastChecked(false);
      disparador(
        UpdatePendiente(
          ItemSelect.id,
          ItemSelect.estado,
          usuario.user.user[0].id_user
        )
      );
    }
  }, [lastChecked]);

  return (
    <>
      <Card className="mb-4">
        <CardBody>
          <div className="mb-5">
            <h5 className="card-title">Cambiar Foto de Perfil</h5>
            <span style={{ color: 'gray' }}>Foto actual</span>
            <Form disabled>
              <FormGroup>
                <Colxx sm={12}>
                  <SingleLightbox
                    thumb="/assets/img/profiles/l-1.jpg"
                    large="/assets/img/profiles/1.jpg"
                    className="card-img list-thumbnail"
                  />
                </Colxx>
              </FormGroup>
              <FormGroup row>
                <Colxx sm={12} className="">
                  <span className="d-block" style={{ color: 'gray' }}>
                    Cargar nueva foto
                  </span>
                  <Button color="primary" className="mb-2 d-block">
                    Seleccionar Foto
                  </Button>{' '}
                </Colxx>
              </FormGroup>
            </Form>
          </div>
        </CardBody>
      </Card>
    </>
  );
};

export default UploadPhoto;
