import React, { useState, useEffect } from 'react';
import { Card, CardBody, CardText } from 'reactstrap';
import IntlMessages from '../../helpers/IntlMessages';
import { useDispatch, useSelector } from 'react-redux';
import { InsertPendiente, UpdatePendiente } from '../../Dusk/DashboardDusk';
import { Colxx } from '../../components/common/CustomBootstrap';
import Form from 'reactstrap/lib/Form';
import FormGroup from 'reactstrap/lib/FormGroup';
import Label from 'reactstrap/lib/Label';
import Input from 'reactstrap/lib/Input';
import Button from 'reactstrap/lib/Button';

const EditarPerfil = () => {
  const disparador = useDispatch();
  const [lastChecked, setLastChecked] = useState(false);
  const [TituloNormal, setTituloNormal] = useState();
  const [ItemSelect, setItemSelect] = useState({ estado: '', id: '' });
  const usuario = useSelector((store) => store.Dash);
  const insertPendientre = async () => {
    disparador(InsertPendiente(usuario.user.user[0].id_user, TituloNormal));
    setTituloNormal('');
  };

  useEffect(() => {
    if (lastChecked === true) {
      setLastChecked(false);
      disparador(
        UpdatePendiente(
          ItemSelect.id,
          ItemSelect.estado,
          usuario.user.user[0].id_user
        )
      );
    }
  }, [lastChecked]);

  return (
    <>
      <Card className="mb-4">
        <CardBody>
          <div className="mb-5">
            <h5 className="card-title">Actualiza tu Información</h5>
            <Form disabled>
              <FormGroup row>
                <Colxx sm={12}>
                  <FormGroup>
                    <Label for="exampleEmailGrid">
                      <IntlMessages id="forms.email" />
                    </Label>
                    <Input
                      type="email"
                      name="exampleEmailGrid"
                      id="exampleEmailGrid"
                      disabled
                      placeholder="Correo@gmail.com"
                    />
                  </FormGroup>
                </Colxx>

                <Colxx sm={12}>
                  <FormGroup>
                    <Label for="exampleAddressGrid">
                      <IntlMessages id="forms.telephone" />
                    </Label>
                    <Input
                      type="text"
                      name="telephoneGrid"
                      id="telephone"
                      placeholder="322 432 3443"
                      disabled
                    />
                  </FormGroup>
                </Colxx>

                <Colxx sm={12}>
                  <FormGroup>
                    <Label for="examplePasswordGrid">
                      <IntlMessages id="forms.password" />
                    </Label>
                    <Input
                      type="password"
                      name="examplePasswordGrid"
                      id="examplePasswordGrid"
                      disabled
                      // placeholder={messages['forms.password']}
                    />
                  </FormGroup>
                </Colxx>

                <Colxx sm={12}>
                  <FormGroup>
                    <Label for="examplePasswordGrid">
                      <IntlMessages id="forms.password" />
                    </Label>
                    <Input
                      type="password"
                      name="examplePasswordGrid"
                      id="examplePasswordGrid"
                      disabled
                      // placeholder={messages['forms.password']}
                    />
                  </FormGroup>
                </Colxx>
              </FormGroup>
              <Button outline color="primary" className="mb-2">
                Actualizar
                {/* <IntlMessages id="blanco.back" /> */}
              </Button>{' '}
            </Form>
          </div>
        </CardBody>
      </Card>
    </>
  );
};

export default EditarPerfil;
